import React, { useState, useEffect, createContext } from 'react';
import { LightTheme, DarkTheme } from '../theme';
import AsyncStorage from '@react-native-async-storage/async-storage';

// Cration du contexte de thme
export const ThemeContext = createContext();

// Provider du thme
export const ThemeProvider = ({ children }) => {
  // tat pour le thme actuel
  const [isDarkMode, setIsDarkMode] = useState(false);
  const [theme, setTheme] = useState(LightTheme);
  const [isLoading, setIsLoading] = useState(true);

  // Charger les prfrences de thme au dmarrage
  useEffect(() => {
    const loadThemePreference = async () => {
      try {
        setIsLoading(true);
        const themePreference = await AsyncStorage.getItem('themePreference');
        
        if (themePreference === 'dark') {
          setIsDarkMode(true);
          setTheme(DarkTheme);
        } else {
          setIsDarkMode(false);
          setTheme(LightTheme);
        }
      } catch (error) {
        console.error('Erreur lors du chargement des prfrences de thme:', error);
        // Par dfaut, utiliser le thme clair
        setIsDarkMode(false);
        setTheme(LightTheme);
      } finally {
        setIsLoading(false);
      }
    };

    loadThemePreference();
  }, []);

  // Fonction pour basculer entre les thmes
  const toggleTheme = async () => {
    try {
      const newIsDarkMode = !isDarkMode;
      setIsDarkMode(newIsDarkMode);
      setTheme(newIsDarkMode ? DarkTheme : LightTheme);
      
      // Sauvegarder la prfrence
      await AsyncStorage.setItem('themePreference', newIsDarkMode ? 'dark' : 'light');
    } catch (error) {
      console.error('Erreur lors de la sauvegarde des prfrences de thme:', error);
    }
  };

  // Fonction pour dfinir un thme spcifique
  const setThemeMode = async (mode) => {
    try {
      const isDark = mode === 'dark';
      setIsDarkMode(isDark);
      setTheme(isDark ? DarkTheme : LightTheme);
      
      // Sauvegarder la prfrence
      await AsyncStorage.setItem('themePreference', mode);
    } catch (error) {
      console.error('Erreur lors de la sauvegarde des prfrences de thme:', error);
    }
  };

  return (
    <ThemeContext.Provider
      value={{
        theme,
        isDarkMode,
        toggleTheme,
        setThemeMode,
        isLoading
      }}
    >
      {children}
    </ThemeContext.Provider>
  );
};
